@import "normalize.css";
:root {
  --swiper-pagination-color: #333;
  --swiper-pagination-bullet-inactive-color: #333;
  --swiper-pagination-bullet-width: 14px;
  --swiper-pagination-bullet-height: 14px;
  --swiper-pagination-bullet-horizontal-gap: 8px;
  --swiper-pagination-bullet-inactive-opacity: .3;
}

@font-face {
  font-family: "PlayfairDisplayBlack";
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: local("PlayfairDisplayBlack"), url("fonts/PlayfairDisplay-Black.woff2") format("woff2"), url("fonts/PlayfairDisplay-Black.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("OpenSansRegular"), url("fonts/open-sans-v35-cyrillic_latin-regular.woff2") format("woff2"), url("fonts/open-sans-v35-cyrillic_latin-regular.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("OpenSansSemiBold"), url("fonts/open-sans-v35-cyrillic_latin-600.woff2") format("woff2"), url("fonts/open-sans-v35-cyrillic_latin-600.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("OpenSansBold"), url("fonts/open-sans-v35-cyrillic_latin-700.woff2") format("woff2"), url("fonts/open-sans-v35-cyrillic_latin-700.woff") format("woff");
}
@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img,
svg {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.stop-scroll {
  overflow: hidden;
}

body {
  min-width: 320px;
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1;
  background-color: #fff;
}

.container {
  max-width: 1352px;
  padding: 0 36px;
  margin: 0 auto;
}

.btn {
  padding: 0;
  cursor: pointer;
  background-color: transparent;
  border: 2px #A65CF0 solid;
  border-radius: 10px;
}

.grid {
  display: grid;
  gap: 32px;
}

.flex {
  display: flex;
}

