.header__top-wrapper {
  padding: 17px 0 15px 0;
  background-color: #F6F6F6;
}
.header__middle-wrapper {
  padding: 32px 0;
}
.header__logo-tablet {
  display: none;
}

.header-top {
  flex-wrap: wrap;
}
.header-top__label {
  align-items: flex-start;
  margin-right: 33px;
}
.header-top__region {
  margin-right: 3px;
  color: #333;
  font-size: 16px;
  line-height: 19px;
}
.header-top__tel-info {
  align-items: center;
  margin-right: auto;
  outline: none;
}
.header-top__tel-info:active .header-top__svg path {
  fill: #BB370E;
}
.header-top__tel-info:focus-visible {
  background-color: #FFF5D1;
}
.header-top__tel-info:active .header-top__phone {
  color: #BB370E;
}
.header-top__phone {
  display: inline-block;
  line-height: 14px;
  color: #FF862F;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
  outline: none;
}
.header-top__svg {
  display: inline-block;
  margin-right: 3px;
