}
.header-top__svg path {
  transition: fill 0.2s ease-in-out;
}
.header-top__list {
  flex-wrap: wrap;
}
.header-top__link {
  display: block;
  color: #999;
  line-height: 14px;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
  outline: none;
}
.header-top__link:focus-visible {
  color: #7033AC;
  background-color: #EBD1FF;
}
.header-top__link:active {
  color: #7033AC;
}
.header-top__item:not(:last-child) {
  margin-right: 64px;
}
.header-top__user {
  display: none;
}
.header-top__basket {
  display: none;
}

.header-middle {
  align-items: center;
  flex-wrap: wrap;
}
.header-middle__logo {
  display: flex;
  margin-right: 141px;
}
.header-middle__logo:focus-visible {
  outline-offset: 4px;
  outline: 2px #A65CF0 solid;
}
.header-middle__svg {
  width: 187px;
  height: 27px;
}
.header-middle__nav-wrapper {
  flex-grow: 1;
}
.header-middle__close {
  display: none;
}
.header-middle__nav-two {
  display: none;
}
.header-middle__nav {
  flex-grow: 1;
}
.header-middle__list {
  flex-wrap: wrap;
  justify-content: space-between;
  flex-grow: 1;
}
.header-middle__link {
  line-height: 22px;
  font-size: 16px;
  color: #333;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
