}
.header-middle__link:focus-visible {
  color: #7033AC;
  background-color: #EBD1FF;
}
.header-middle__link:active {
  color: #7033AC;
}
.header-middle__user-basket {
  display: none;
}

.header-bottom {
  flex-wrap: wrap;
}
.header-bottom__form-select {
  margin-right: 120px;
  flex-grow: 1;
  flex-shrink: 1;
  align-items: center;
}
.header-bottom__form {
  height: 100%;
  position: relative;
  flex-grow: 1;
  flex-shrink: 1;
}
.header-bottom__search-btn {
  width: 32px;
  height: 32px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 32px;
  border: none;
  background-color: transparent;
  outline: none;
  border-radius: 50%;
  transition: background-color 0.2s ease-in-out;
}
.header-bottom__search-btn:focus-visible .header-bottom__search-svg path {
  stroke: #fff;
}
.header-bottom__search-btn:focus-visible {
  background-color: #C998FB;
}
.header-bottom__search-btn:active .header-bottom__search-svg path {
  stroke: #fff;
}
.header-bottom__search-btn:active .header-bottom__search-svg circle {
  stroke: #7033AC;
}
.header-bottom__search-btn:active {
  background-color: #7033AC;
}
.header-bottom__search-btn:disabled .header-bottom__search-svg path {
  stroke: #999;
}
.header-bottom__search-btn:disabled .header-bottom__search-svg circle {
  stroke: #999;
}
.header-bottom__search-btn:disabled {
  background-color: transparent;
}
.header-bottom__search {
  height: 100%;
  width: 100%;
  max-width: 761px;
  border: 2px solid #F6F6F6;
  padding: 13px 65px 13px 59px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  background-color: #F6F6F6;
  background-image: url("img/search.svg");
  background-repeat: no-repeat;
  background-size: 16px;
  background-position: left 33px center;
  flex-grow: 1;
  flex-shrink: 1;
  color: #333;
  font-size: 16px;
  line-height: 19px;
  outline: none;
  transition: background-color 0.2s ease-in-out, background-image 0.2s ease-in-out, border-color 0.2s ease-in-out;
}
.header-bottom__search::placeholder {
  color: #999;
}
.header-bottom__search:focus-visible {
  background-color: #F8EFFF;
  border-color: #A65CF0;
}
.header-bottom__search:active {
  background-image: url("img/search-active.svg");
}
.header-bottom__search-svg {
  width: 32px;
  height: 32px;
}
.header-bottom__search-svg circle {
  transition: stroke 0.2s ease-in-out;
}
.header-bottom__search-svg path {
  transition: stroke 0.2s ease-in-out;
}
.header-bottom__user {
  position: relative;
  margin-right: 29px;
  border: none;
  align-self: center;
  outline: none;
  border-radius: 0;
  z-index: 5;
}
.header-bottom__user::before {
  content: "";
  position: absolute;
  width: 34px;
  height: 36px;
  top: -4px;
  left: -4px;
  bottom: -4px;
  right: -4px;
  z-index: -1;
  transition: background-color 0.2s ease-in-out;
}
.header-bottom__user:focus-visible::before {
  background-color: #C998FB;
}
.header-bottom__user:active::before {
  display: none;
}
.header-bottom__user:focus-visible .header-bottom__user-svg path {
  fill: #fff;
}
.header-bottom__user:active .header-bottom__user-svg path {
  fill: #7033AC;
}
.header-bottom__user-svg {
  width: 26px;
  height: 28px;
}
.header-bottom__user-svg path {
  transition: fill 0.2s ease-in-out;
}
.header-bottom__counter {
  padding: 3px 4px 3px 4px;
  position: relative;
  width: 14px;
  height: 16px;
  top: 6px;
  left: 3px;
  border-radius: 50%;
  color: #fff;
  background-color: #FF862F;
  font-weight: 700;
  font-size: 10px;
  line-height: 10px;
}
.header-bottom__basket {
  border: none;
  position: relative;
  align-self: center;
  border-radius: 0;
  outline: none;
  z-index: 5;
}
.header-bottom__basket::before {
  content: "";
  position: absolute;
  width: 49px;
  height: 35px;
  top: -4px;
  left: -1px;
  bottom: -3px;
  right: -7px;
  z-index: -1;
  transition: background-color 0.2s ease-in-out;
}
.header-bottom__basket:focus-visible::before {
  background-color: #C998FB;
}
.header-bottom__basket:active::before {
  display: none;
}
.header-bottom__basket:focus-visible .header-bottom__basket-svg path {
  fill: #fff;
}
.header-bottom__basket:active .header-bottom__basket-svg path {
  fill: #7033AC;
}
.header-bottom__basket-svg {
  width: 30px;
  height: 28px;
}
.header-bottom__basket-svg path {
  transition: fill 0.2s ease-in-out;
}

.burger {
