  border-radius: 2px;
  margin-right: 9px;
  vertical-align: middle;
  transition: background-color 0.2s ease-in-out;
}
.filter__visible-checkbox::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 12px;
  height: 7px;
  border-left: 2px solid #fff;
  border-bottom: 2px solid #fff;
  opacity: 0;
  transform: translate(-45%, -65%) rotate(-45deg);
  transition: opacity 0.2s ease-in-out;
}
.filter .filter__checkbox-input:focus-visible + .filter__visible-checkbox {
  outline: 2px solid #C998FB;
  outline-offset: 1px;
}
.filter .filter__checkbox-input:checked ~ .filter__visible-checkbox::before {
  opacity: 1;
}
.filter .filter__checkbox-input:checked ~ .filter__visible-checkbox {
  background-color: #A65CF0;
  border-color: #A65CF0;
}
.filter__card {
  width: 100%;
  min-height: 360px;
  padding: 220px 67px 30px 30px;
  background-color: #333;
  background-image: url("img/filter.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  border-radius: 10px;
}
.filter__card-title {
  margin: 0;
  margin-bottom: 12px;
  color: #999;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}
.filter__card-span {
  display: block;
  font-family: "PlayfairDisplayBlack", "Times New Roman", serif;
  font-weight: 900;
  font-size: 24px;
  line-height: 32px;
  color: #D5B986;
}
.filter__link {
  display: flex;
  align-items: center;
  outline: none;
}
.filter__link:active .filter__link-svg path {
  stroke: #fff;
}
.filter__link:active .filter__link-svg {
  background-color: #7033AC;
}
.filter__link:active .filter__link-svg circle {
  stroke: #7033AC;
}
.filter__link:active .filter__link-span::after {
  transform: scaleX(1);
  background-color: #7033AC;
}
.filter__link:focus-visible .filter__link-svg path {
  stroke: #fff;
}
.filter__link:focus-visible .filter__link-svg {
  background-color: #C998FB;
}
.filter__link:focus-visible .filter__link-span::after {
  transform: scaleX(1);
}
.filter__link:active .filter__link-span {
  color: #7033AC;
}
.filter__link-span {
  position: relative;
  display: block;
  margin-right: 5px;
  color: #A65CF0;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
  outline: none;
  transition: color 0.2s ease-in-out;
}
.filter__link-span::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #A65CF0;
  transform: scaleX(0);
  transition: transform 0.2s ease-in-out, background-color 0.2s ease-in-out;
}
.filter__link-svg {
  border-radius: 50%;
  transition: background-color 0.2s ease-in-out;
}
.filter__link-svg path {
  transition: stroke 0.2s ease-in-out;
}
.filter__link-svg circle {
  transition: stroke 0.2s ease-in-out;
}
.filter__title_mobile {
  display: none;
}

.noUi-target {
  border: none;
  box-shadow: none;
}

.noUi-connect {
  background-color: #A65CF0;
  transition: background-color 0.2s ease-in-out;
}

.noUi-horizontal .noUi-handle {
  position: absolute;
  right: -6px;
  top: 0;
  height: 12px;
  width: 12px;
  border: none;
  border-radius: 0;
  background-color: transparent;
  outline: none;
  box-shadow: none;
  cursor: pointer;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("img/slider.svg");
  transition: background-image 0.2s ease-in-out;
}
.noUi-horizontal .noUi-handle::after {
  display: none;
}
.noUi-horizontal .noUi-handle::before {
  display: none;
}
.noUi-horizontal .noUi-handle:focus-visible {
  background-image: url("img/slider-active.svg");
}
.noUi-horizontal .noUi-handle:active {
  background-image: url("img/slider-active.svg");
}

